/* eslint-disable no-use-before-define */
import { combineReducers, createStore } from "redux";
import axios from "axios";

const FETCH_TAGS = "FETCH_TAGS";
const FETCH_BIO = "FETCH_BIO";
const FETCH_TAG_TOP_ARTISTS = "FETCH_TAG_TOP_ARTISTS";

const initial = {
  artist: {},
  tags: {},
  tagsBio: {},
  tagsTopArtists: {}
};

export const fetchTags = () =>
  axios
    .get(
      `https://ws.audioscrobbler.com/2.0/?method=user.gettoptags&user=rj&api_key=32294161a44c0621caf3a01a6e94b1a5&format=json`
    )
    .then(response => {
      store.dispatch({
        type: FETCH_TAGS,
        data: response.data.toptags.tag
      });
    })
    .catch(error => {
      store.dispatch({
        type: FETCH_TAGS,
        data: error.response
      });
    });



export const fetchTopTagsBio = tagName =>
  axios
    .get(
      `https://ws.audioscrobbler.com//2.0/?method=tag.getinfo&tag=${tagName}&api_key=32294161a44c0621caf3a01a6e94b1a5&format=json`
    )
    .then(response => {
      store.dispatch({
        type: FETCH_BIO,
        name: tagName,
        data: response.data.tag.wiki.summary
      });
    })
    .catch(error => {
      store.dispatch({
        type: FETCH_BIO,
        data: error.response
      });
    });

export const fetchTagTopArtists = tagName =>
  axios
    .get(
      `https://ws.audioscrobbler.com/2.0/?method=tag.gettopartists&tag=${tagName}&api_key=32294161a44c0621caf3a01a6e94b1a5&format=json`
    )
    .then(response => {
      store.dispatch({
        type: FETCH_TAG_TOP_ARTISTS,
        name: tagName,
        data: response.data.topartists.artist
      });
    })
    .catch(error => {
      store.dispatch({
        type: FETCH_TAG_TOP_ARTISTS,
        data: error.response
      });
    });

const fetchTagsReducer = (state = initial, action) => {
  switch (action.type) {
    case FETCH_TAGS:
      return {
        tags: {
          ...state.tags,
          toptags: action.data
        }
      };
    default:
      return state;
  }
};

const fetchTagsBioReducer = (state = initial, action) => {
  switch (action.type) {
    case FETCH_BIO:
      return {
        tagsBio: {
          ...state.tagsBio,
          [action.name]: action.data.split(" <a")[0]
        }
      };
    default:
      return state;
  }
};

const fetchTagTopArtistReducer = (state = initial.tagsTopArtists, action) => {
  switch (action.type) {
    case FETCH_TAG_TOP_ARTISTS:
      return {
        ...state,
        tagTopArtists: {
          ...state.tagTopArtists,
          [action.name]: action.data
        }
      };
    default:
      return state;
  }
};
export const rootReducer = combineReducers({
  tags: fetchTagsReducer,
  bio: fetchTagsBioReducer,
  tagTopArtists: fetchTagTopArtistReducer
});

const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__;

let store = null;

if (process.env.NODE_ENV === "production") {
  store = createStore(rootReducer);
} else {
  store = createStore(rootReducer, composeEnhancers());
}
export default store;
