import React, { Component } from "react";
import { connect } from "react-redux";
import { bioFormater } from "../../helpers/workmates";
class TopArtist extends Component {
  state = {
    bio: bioFormater(this.props.bio[this.props.tagName]),
    read: "more"
  };
  displayAllBio() {
    if (this.state.read === "more") {
      this.setState({
        bio: this.props.bio[this.props.tagName],
        read: "less"
      });
    } else {
      this.setState({
        bio: bioFormater(this.props.bio[this.props.tagName]),
        read: "more"
      });
    }
  }
  render() {
    const style = {
      background: `repeating-linear-gradient(
        to bottom,
        rgba(0, 0, 0, 0.67),
        rgba(0, 0, 0, 0.67)
      ), 
      url(${
        this.props.tagTopArtists[this.props.tagName][0].image[4]["#text"]
      })`,
      backgroundSize: "cover"
    };
    return (
      <div className="top-artist" style={style}>
        <div className="artist-info">
          <div className="tag-link">
            <a href={"https://google.com"} alt={""} style={{color: "white"}}>
              {this.props.tagName.toUpperCase()}
            </a>
          </div>
          <div>
            Featured Artist:{" "}
            <span style={{ fontWeight: "bold" }} className="artist-name">
              {this.props.tagTopArtists[this.props.tagName][0].name}
            </span>
          </div>
          <div className="bio">
            {this.state.bio}
            <div className="readmore">
              <span className="read" onClick={() => this.displayAllBio()}>
                read {this.state.read}
              </span>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
const mapStateToProps = state => ({
  bio: state.bio.tagsBio || {},
  tagTopArtists: state.tagTopArtists.tagTopArtists || {}
});

const TagTopArtistContainer = connect(
  mapStateToProps,
  null
)(TopArtist);

export default TagTopArtistContainer;
