import React, { Component } from "react";
import Loader from "react-loader-spinner";
import TopArtist from "./topartist";
import SmallTagList from "./smalltagslist";
import SecondArtist from "./secondartist";
import { connect } from "react-redux";
import { fetchTags } from "../../redux/store";
import { fetchTopTagsBio } from "../../redux/store";
import { fetchTagTopArtists } from "../../redux/store";

class Tags extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true
    };
  }
  componentDidMount() {
    fetchTags().then(() => {
      Promise.all([
        fetchTopTagsBio(this.props.tags[0].name),
        fetchTagTopArtists(this.props.tags[0].name),
        fetchTopTagsBio(this.props.tags[1].name),
        fetchTagTopArtists(this.props.tags[1].name)
      ]).then(() => {
        this.setState({
          loading: false
        });
      });
    });
  }
  renderContent() {
    const tag = this.props.tags;
    const firstTag = tag[0];
    const secondTag = tag[1];
    const topTagRow1 = [tag[2], tag[3], tag[4]];
    const topTagRow2 = [tag[5], tag[6], tag[7]];
    const topTagRow3 = [tag[8], tag[9], tag[10]];
    const topTagRow4 = [tag[11], tag[12], tag[13]];
    const topTagRow5 = [tag[14], tag[15], tag[16]];
    const topTagRow6 = [tag[17], tag[18], tag[19]];
    if (this.state.loading) {
      return (
        <div className="loader-container">
          <Loader type="Audio" color="#black" height="250" width="250" />
        </div>
      );
    }
    return (
      <div>
        <div className="tags-container1">
          <div className="top-artist1">
            <TopArtist tagName={firstTag.name} loading={this.state.loading} />
          </div>

          <div className="grid-container">
            <div className="row">
              <SmallTagList tags={topTagRow1} />
            </div>
            <div className="row">
              <SmallTagList tags={topTagRow2} />
            </div>
            <div className="row">
              <SmallTagList tags={topTagRow3} />
            </div>
          </div>
        </div>
        <div className="tags-container2">
          <div className="grid-container">
            <div className="row">
              <SmallTagList tags={topTagRow4} />
            </div>
            <div className="row">
              <SmallTagList tags={topTagRow5} />
            </div>
            <div className="row">
              <SmallTagList tags={topTagRow6} />
            </div>
          </div>
          <div className="top-artist2">
            <SecondArtist
              tagName={secondTag.name}
              loading={this.state.loading}
            />
          </div>
        </div>
      </div>
    );
  }
  render() {
    return (
      <div className="app-container">
        <div className="section-title">
          <div className="title">
            <div className="title-string">Tags to explore</div>
            <div className="line" />
          </div>
        </div>
        {this.renderContent()}
      </div>
    );
  }
}

const mapStateToProps = state => ({
  tags: state.tags.tags.toptags || []
});

const TagContainer = connect(
  mapStateToProps,
  null
)(Tags);

export default TagContainer;
