import React, { Component } from "react";
import "antd/dist/antd.css";
import HeaderMenu from "./headermenu";
import SearchBar from "./searchbar";

class Header extends Component {
  state = {
    searchVisibility: false
  };

  handleSearchBarVisibility(value) {
    this.setState({
      searchVisibility: value
    });
  }

  render() {
    if (this.state.searchVisibility === false) {
      return (
        <HeaderMenu change={value => this.handleSearchBarVisibility(value)} />
      );
    } else {
      return (
        <SearchBar change={value => this.handleSearchBarVisibility(value)} />
      );
    }
  }
}

export default Header;
